.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 25px;
}
    
.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}
    
.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 25px;
}
    
.slider:before {
    position: absolute;
    content: "";
    height: 18px;
    width: 18px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 50%;
}
    
input:checked + .slider {
    background-color: #2196F3;
}
    
input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
}
    
input:checked + .slider:before {
    -webkit-transform: translateX(33px);
    -ms-transform: translateX(33px);
    transform: translateX(33px);
}

.Toggle {
    margin-top: 5px;
    margin-bottom: 15px;
    position: relative;
}

.Calc {
    display: inline-block;
    font-size: 24px;
    margin-left: 15px;
    position: relative;
    top: 6px;
}